.square {
    background:  rgb(119,149,86);
    border: 3px solid rgb(0, 0, 0);
    -webkit-text-fill-color:bisque;
    -webkit-text-stroke: 5px #000;
    float: left;
    font-size: 200px;
    font-weight: bold;
    line-height: 34px;
    height: 200px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 200px;
    border-radius: 10px 10px 10px 10px;
}